<section class="services-our" id="services-our"><div class="container"><div class="services-our-wrap"><div class="services-our__title"><section-title :title="servicesOur.title" :subtitle="servicesOur.subtitle"></section-title></div><div class="services-our__items"><div class="services-our__item" v-for="item in servicesOurItems" :key="item.id"><div class="services-our__item-img"> <img :src="require('@/assets/img/' + item.src)" :alt="item.alt"></div><h4 class="services-our__item-title">{{ item.title }}</h4><p class="services-our__item-desc">{{ item.desc }}</p></div></div></div></div></section>