<section class="sidebar" id="sidebar"><div class="container"><div class="sidebar-wrap"><div class="sidebar__title"><section-title :title="sidebar.title" :subtitle="sidebar.subtitle"></section-title></div><div class="sidebar__links"><ul class="sidebar__links-row"><li class="sidebar__links-item" v-for="item in navLinks"><router-link class="sidebar__links-item-link" :to="item.link" @click="hideScroll(), scrollToTop()"><span>{{ item.text }}</span></router-link></li></ul></div><div class="sidebar__login"><btn-double :btnText="navbtn" @action="formLoginUnlock()"></btn-double></div></div></div></section>