<section class="subscribe" id="subscribe"><div class="container"><div class="subscribe-wrap"><div class="subscribe__title"><section-title :title="sectionSubscribe.title" :subtitle="sectionSubscribe.subtitle"></section-title></div><form class="subscribe__form" @submit="validFormSubscribe($event)"><input class="subscribe__form-input" id="subscribeFormName" type="text" :placeholder="sectionSubscribe.namePlaceholder" v-model="nameSubs"><input class="subscribe__form-input" id="subscribeFormEmail" type="email" :placeholder="sectionSubscribe.emailPlaceholder" v-model="emailSubs"><button class="subscribe__form-btn">{{ sectionSubscribe.btnText }}</button></form></div></div></section>