<section class="navigation" id="navigation"><div class="container"><div class="navigation-wrap"><div class="navigation__logo"><router-link class="navigation__logo-link" to="/" @click="scrollToTop()"><img class="navigation__logo-link-img" :src="require('@/assets/img/' + logo.src)" :alt="logo.alt"></router-link></div><div class="navigation__nav"><ul class="navigation__nav-row"><li class="navigation__nav-item" v-for="item in navLinks"><router-link class="navigation__nav-item-link" :to="item.link" @click="scrollToTop()"><span>{{ item.text }}</span></router-link></li></ul></div><div class="navigation__form"><btn-double :btnText="navbtn" @action="formLoginUnlock(), scrollToTop()"></btn-double></div><button class="navigation__burger" @click="unlockSidebar(), hideScroll()"><span></span></button></div></div></section>