<section class="header" id="header"><div class="header-bg"><div class="header-bg-img" v-for="item in headerBg" :key="item"><img :src="require('@/assets/img/' + item.src)" :alt="item.alt"></div></div><div class="container"><div class="header-wrap"><div class="header-col"><h2 class="header__title">{{ headerTitle }}</h2><h4 class="header__subtitle">{{ headerSubtitle }}</h4><div class="header__btns"><btn-download :link="googleStore.href" :text="googleStore.text"></btn-download><router-link class="header-btn" to="/blog">Learn More</router-link></div></div><div class="header__phone"><div class="header-elipses"><span v-for="item in 4"></span></div></div></div></div></section>