<section class="services-other" id="services-other"><div class="container"><div class="services-other-wrap"><div class="services-other__title"><section-title :title="servicesOther.title" :subtitle="servicesOther.subtitle"></section-title></div><div class="services-other__list-wrap"><ul class="services-other__list"><li class="services-other__list-item" v-for="item in servicesOther.list"><div class="services-other__list-img"><img :src="require('@/assets/img/' + item.src)"></div><div class="services-other__list-text"><h4 class="services-other__list-title">{{ item.title }}</h4><p class="services-other__list-desc">{{ item.desc }}</p></div></li></ul></div></div></div></section>