<section class="comments" id="comments"><div class="container"><div class="comments-wrap"><div class="comments__title"><section-title :title="comments.title" :subtitle="comments.subtitle"></section-title></div><div class="comments__content"><div class="comments__content-slider"><ul class="comments__content-slider__list"><li class="comments__content-slider__item" v-for="item in comments.slides" :key="item" :data-pos="item.idx" @click="getActiveSlide($event)"><div class="comments__content-slide-avatar"><img :src="require('@/assets/img/' + item.author)"></div><h4 class="comments__content-slide-name">{{ item.name }}</h4><h5 class="comments__content-slide-subtitle">{{ item.subtitle }}</h5><p class="comments__content-slide-text">{{ item.text }}</p></li></ul><div class="comments__content-slider-arrow"><button class="slider-arrow slider-arrow--prev" @click="prevSlide()"><img src="@/assets/img/comments/arrow.png"><span>{{ comments.sliderArrow.prev }}</span></button><button class="slider-arrow slider-arrow--next" @click="nextSlide()"><span>{{ comments.sliderArrow.next }}</span><img src="@/assets/img/comments/arrow.png"></button></div></div></div></div></div></section>