<section class="features" id="features"><div class="container"><div class="features-wrap"><div class="features__info"><h5 class="features__info-subtitle">{{ featuresInfo.subtitle }}</h5><h3 class="features__info-title">{{ featuresInfo.title }}</h3><p class="features__info-desc">{{ featuresInfo.desc }}</p><ul class="features__info-list"><li class="features__info-list-item" v-for="item in featuresInfo.list"><span>{{ item }}</span></li></ul><button class="features__info-btn">{{ featuresInfo.btn }}</button></div></div></div></section>