<section class="footer" id="footer"><div class="container"><div class="footer-wrap"><div class="footer-col"><div class="footer__logo"><router-link :to="sectionFooter.logo.href"><img :src="require('@/assets/img/' + sectionFooter.logo.src)"></router-link></div><div class="footer__desc"> <p>{{ sectionFooter.desc }}</p></div><div class="footer__social"><div class="footer__social-item" v-for="item in sectionFooter.social"><a class="footer__social-item-link"  :href="item.link"><img :src="require('@/assets/img/' + item.src)"></a></div></div></div><div class="footer-col" v-for="list in sectionFooter.list"><div class="footer__list"><h4 class="footer__list-title">{{ list.title }}</h4><a class="footer__list-link"  v-for="item in list.links" :href="item.href">{{ item.text }}</a></div></div></div></div></section>