<section class="price-cards" id="price-cards"><div class="container"><div class="price-cards-wrap"><div class="price-cards__title"><section-title :title="priceCards.title" :subtitle="priceCards.subtitle"></section-title></div><div class="price-cards__list"><ul class="price-cards__list-wrap"><li class="price-cards__list-item" v-for="item in priceCards.list" :key="item"><div class="price-cards__list-img"><img :src="require('@/assets/img/' + item.src)" :alt="item.alt"></div><h3 class="price-cards__list-name">{{ item.name }}</h3><h5 class="price-cards__list-price"><span>$</span><span>{{ item.price }}</span><span>/mo</span></h5><ul class="price-cards__list-tags"><li class="price-cards__list-tags-item" v-for="link in item.listCard" :key="item">{{ link }}</li></ul><button class="price-cards__list-btn">{{ item.btn }}</button></li></ul></div></div></div></section>